<template>
    <nav class="nav">
        <a href="https://github.com/" class="nav__logo">
            <button data-text="Awesome" class="button">
            <span class="actual-text">&nbsp;github&nbsp;</span>
            <span class="hover-text" aria-hidden="true">&nbsp;github&nbsp;</span>
            </button>
        </a>
        <ul class="MediumBlock__menu" style="display: flex; align-items: center; gap: 5px;">
            <!-- <li v-for="item in links" :key="item.url">
                <router-link :to="item.url"><button class="MediumBlock__menu-a-btn">{{ item.name }}</button></router-link>
            </li> -->
            <div class="searchBox">
                <input class="searchInput" type="text" name="" placeholder="Search">
                    <btn class="searchButton" href="#">
                    <i class="material-icons">
                    search
                   </i>
                </btn>
            </div>
        </ul>
    </nav>
    
    <header class="header">
        <div class="container">
            <div class="MinBlock__input">
                <input 
                type="text" 
                class="header__search" 
                placeholder="Введите имя пользователя" 
                v-model="getUser"
                @keydown.enter="getInfo(getUser)">
                <button class="header__search-button" @click="getInfo(getUser)">НАЙТИ</button>
            </div>
        </div>
    </header>
    <MainCurrent/>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
import MainCurrent from './MainCurrent.vue';

    export default {
        components: { MainCurrent,  },
        computed:{
       ...mapState(['user']),
       getUser: {
    get(){ 
    return this.user
    },
    set(val){
       this.setUser(val)
    }
       }
        },
        methods: {
            ...mapMutations(['setUser']),
            ...mapActions(['getInfo']),
        },
    }
</script>

