import axios from 'axios';
import { createStore } from 'vuex'

export default createStore({
  state: {
    user: '',
    info: null,
    repositories: []
  },
  getters: {
   
  },
  mutations: {
    setUser(state,user){
    state.user = user
    },
    getInfo(state, UserInfo ){
state.info = UserInfo

    },
    setRepositories(state, repositories){
      state.repositories = repositories
    }
  
  },
  actions: {



   async getInfo(context, user){
   try {
    let response = await  axios.get(` https://api.github.com/users/${user}`)
  
let UserInfo = {
...response.data,
name: user
}
console.log(UserInfo);
context.commit('getInfo',UserInfo)







let reposRespons = await axios.get(`https://api.github.com/users/${user}/repos?per_page=5`)
let repositories = reposRespons.data
context.commit('setRepositories', repositories)
  

 
   } catch (error) {
    console.log("ошибка при запросе" + error);
   }


    


   }
  },

})

