<template lang="">
  <div v-if="this.$store.state.info">
    <div class="title-block">
      <h2 class='title'>Сортировка</h2>
      <div class="btns-block">
        <div class="home-radio">
        <div class="radio-inputs">
            <label class="radio">
                <input type="radio" name="radio" checked="">
                <span class="name" @click=' sortedRepositoryByName'>ИМЯ</span>
            </label>
            <label class="radio">
                <input type="radio" name="radio">
                <span class="name" @click=' sortedRepositoryByName'>ЗВЕЗДЫ</span>
            </label>
            <label class="radio">
                <input type="radio" name="radio">
                <span class="name" @click='sortedRepository'>ДАТА</span>
            </label>
        </div>
    </div>
         
      </div>
    </div>
   <div class="sort">
    <div class="sort__item" v-for="repositori in repositories" :key="repositori.id">
      <div class="sort__item-info">
        <h2 class="sort__item-title">Имя: {{repositori.name}}</h2>
        <p class="sort__item_text">Кол-во звёзд: {{repositori.forks}}</p>
        <p class="sort__item_text">Дата добавления: {{repositori.created_at}}</p>
      </div>
        <a :href="repositori.html_url" class="Info__left-visit visit-left">ПОСЕТИТЬ</a>
    </div>
   </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex';
export default {
data() {
  return {
    ascedingOrder: true
  }
},
    computed: {
        ...mapState(['repositories']),
    },
    methods: {
      sortedRepository(){
      return this.repositories.sort((a,b) => b.created_at.localeCompare(a.created_at), )
      },
      sortedRepositoryByName(){
      return this.repositories.sort((a,b) => a.name.localeCompare(b.name)) 
   
      },
      sortedRepositoryBystar(){
      return this.repositories.sort((a,b) => a.forks - b.forks) 
    } 
  }
}
</script>

